html,
body {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  /* font-family: "Sorts Mill Goudy", sans-serif; */
  font-family: "Lato", sans-serif;
  /* color: #2aabe4; */
  padding-bottom: 50px;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  justify-content: center;
  text-align: center;
  position: relative;
  color: #35477C;
}

.flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-content{
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 8vh;
}

a {
  color: #35477C;
  text-decoration: none;
  padding: 5px 15px;
  transition: all 0.3s ease-in-out;
}


ul {
  list-style-type: none;
  margin: 0 20px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  list-style:none;
}

h1{
  font-family: 'Inter';
font-style: normal;
font-weight: 700;
font-size: 64px;
line-height: 77px;
text-align: center;

/* Blue */

color: #5682C1;

/* text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
}

p{
  font-family: 'Noto Sans';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 140%;
/* or 28px */

display: flex;
align-items: center;
letter-spacing: 0.01em;

/* Body Text Color */

color: rgba(0, 0, 0, 0.8);
}

button {
  cursor: pointer;
  font-family: inherit;
  background: transparent;
  /* transform-origin: top left; */
}
button:active {
  transform: scale(0.98);
}

button:focus {
  outline: none;
}

.btn:hover {
  opacity: 0.9;
  transition-duration: 0s;
}

button:disabled {
  cursor: not-allowed;
}


.main-container{
  margin-top: 0;
  min-height: 100vh;
  width: 100%;
  /* background-image: url("./pages/pics/fatima_place.jpg"); */
}

#fatima-main{
  height: 95vh;
  width: 100%;
  position: relative;
  background-color: white;
  /* background-color: #F4F7F8; */
  
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

#fatima-main h1{
  font-size: 110px;
  position: relative;
  z-index: 2;
  color: white;
  transform: translate(-23vw, -12vh);
  margin: 0;
  padding: 0;
} 

#fatima-main h3{
  font-size: 48px;
  color: white;
  position: relative;
  z-index: 2;
  transform: translate(18vw, -7vh);
  margin: 0;
  padding: 0;
}
#mainbg{
  position: absolute;
  object-fit: cover;
  left: 50%;
  /* width: 100%; */
  height: 115%;
  transform: translate(-50%,-7vh);
  z-index: 1;
}
#v1-1{
  position: absolute;
  right: 0;
  top: -0;
  height: 92vh;
  max-height: 100vh;
  transform: rotate(180deg);
}
#v1-2{
  position: absolute;
  left: 0;
  top: -0;
  height: 92vh;
  max-height: 100vh;
  /* transform: rotate(180deg); */
}
.hero{
  /* margin-top: 10vh; */
  height: 90vh;
  width: 100%;
  /* background-image: url("./pages/pics/main.jpg"); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.hero-images{
  position: absolute;
display: flex;
height: 90vh;
width: 300vw;
transform: translateX(0);
/* background-color: #000; */
transition: transform 0.5s ease-in-out;
z-index: 5;
}
.hero-images .hero-image{
  height: 90vh;
  width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}
#hero1{
  background-image: url("./pages/pics/main.jpg");
}
#hero2{
  background-image: url("./pages/pics/people.jpg");
  /* width: 101vw; */
  /* transform: translateX(-1vw); */
}
#hero3{
  background-image: url("./pages/pics/place.jpg");
}

.hero-container-dim{
  position: absolute;
  width: 100%;
  height: 90vh;
  background-color: #000;
  opacity: 0.3;
  z-index: 2;
}

.hero-container{
  position: absolute;
  width: 100%;
  height: 90vh;
  color: #fff;
  opacity: 1;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.hero-container > *{
  opacity: 1;
}

.hero-aboutus{
  /* margin-top: 10vh; */
  height: 90vh;
  width: 100%;
  background-image: url("./pages/pics/people.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}
/* header 1 */
#header1 {
  position: fixed;
  background-color: white;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease-in-out;
  padding: 0;
  margin: 0;
  z-index: 10;
  height: 10vh;
  box-shadow: 0px 0px 4px 0px #00000040;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 1.3); */
}

#header1 #logo-fatima{
  display: flex;
  align-items: center;
}
#header1 .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 0; */
  transition: all 0.3s ease-in-out;
  height: 10vh;
}

#header1 h1{
  font-family: "Inter", sans-serif;
  color: #373F51;
  /* font-family: "Secular One", sans-serif; */
  font-size: 40px;
  margin-left: 15px;
  font-stretch: expanded;
  letter-spacing: 0.07em;
}

#header1 ul {
  display: flex;
  list-style-type: none;
  margin: 0 20px;
  align-items: center;
  justify-content: center;
  height: 100%;
}
#header1 li{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 0.5px solid #E3E3E3;
  padding: 0 20px 0 20px;
}

#header1 li:last-of-type{
  border-right: none;
}
#header1 a {
  color: #1B1B1E;
  font-family: "Work+Sans", sans-serif;
  text-decoration: none;
  padding: 5px 15px;
  transition: all 0.3s ease-in-out;
  font-size: 20px;
}

#header1.active {
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 1.3);
  z-index: 100;
  height: 8vh;
}
#header1.active a {
  color: #000;
}
#header1.active .container {
  padding: 5px 0;
}
