#cart-panel{
  position: fixed;
  z-index: 1000;
  background-color: white;
  right: 0.5vw;
  top: 8vh;
  border-radius: 10px;
  min-width: 160px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  text-align: left;
  padding: 10px 15px 10px 15px;
  width: fit-content;
  display: none;
}
#cart-panel.active{
  display: block;
}

#cart-panel p, #cart-panel a{
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  margin: 0;
  color: #2e2e2e;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  z-index: 1001;
}
/* #cart-panel p:last-of-type,  */
#cart-panel a{
  font-weight: normal;
  margin-bottom: 15px;
  z-index: 1001;
}

#cart-panel::before{
    content: "";
    position: fixed;
    width: 31px;
    height: 31px;
    background-color: white;
    border-radius: 2px;
    top: 7vh;
    right: 1.1vw;
    transform: rotate(-45deg);
    /* display: none; */
    /* z-index: 500; */
}
#cart-panel a:hover{
  font-weight: bold;
}



